function sliderGallery() {
    var swiper = new Swiper('.gallery .mySwiper', {
        slidesPerView: "auto",
        spaceBetween: 10,
        navigation: {
            nextEl: ".gallery .swiper-button-next",
            prevEl: ".gallery .swiper-button-prev",
        },
    })
}

$(document).ready(function () {
    sliderGallery();
  
    //Burger
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header-menu').toggleClass('header-menu--active')
        $('body').toggleClass('fixed-body')
    })

    //Select
    $(function() {
        $(".select-hidden .select-item").click(function(e) {
            e.stopPropagation();
            let $item = $(this);
            let html = $item.html();
            $item.closest(".select").find(".select-current").html(html);
            $item.closest(".select").removeClass("select--active");
            $item.siblings(".select-item").removeClass("select-item--active");
            $item.addClass("select-item--active");
        });

        $(".select-current").on("click", function(e) {
            e.stopPropagation();
            let $sel = $(this).closest(".select");
            $(".select").not($sel).removeClass("select--active");
            $sel.toggleClass("select--active");
        });

        $(".select").on("mouseleave", function() {
            $(this).removeClass("select--active");
        });

        $(document).on("click", function(e) {
            if (!$(e.target).closest(".select").length) {
                $(".select").removeClass("select--active");
            }
        });

        var mq = window.matchMedia('(max-width: 992px)');

        function bindSelectShow() {
            $('.select-show').off('.selectShow');

            $(".select").removeClass("select--active");

            if (mq.matches) {
                $('.select-show').on('click.selectShow', function(e) {
                    e.stopPropagation();
                    var $sel = $(this).closest('.select');
                    $(".select").not($sel).removeClass("select--active");
                    $sel.toggleClass("select--active");
                });
            } else {
                $('.select-show').on('mouseenter.selectShow', function(e) {
                    var $sel = $(this).closest('.select');
                    $(".select").not($sel).removeClass("select--active");
                    $sel.addClass("select--active");
                });
            }
        }

        bindSelectShow();
        if (mq.addEventListener) {
            mq.addEventListener('change', bindSelectShow);
        } else if (mq.addListener) { 
            mq.addListener(bindSelectShow);
        }
    });

    //Calendar
    new AirDatepicker('#range', {
        range: true,
        multipleDatesSeparator: ' - ',
        inline: true,
    });
     const $calendar = $('.calendar');
    function addActive() {
        $calendar.addClass('calendar--active');
    }
    function removeActive() {
        $calendar.removeClass('calendar--active');
    }
    function handleClickOutside(e) {
        if (!$calendar.is(e.target) && $calendar.has(e.target).length === 0) {
            removeActive();
        }
    }
    const isDesktop = window.matchMedia('(min-width: 993px)');
    function handleMediaChange() {
        $calendar.off('mouseenter mouseleave click');
        $(document).off('click', handleClickOutside);

        if (isDesktop.matches) {
            $calendar.hover(addActive, removeActive);
        } else {
            $calendar.on('click', addActive);
            $(document).on('click', handleClickOutside);
        }
    }
    isDesktop.addEventListener('change', handleMediaChange);
    handleMediaChange();

    //Video
    const section = document.querySelector('.video-section');
    const video = section.querySelector('#video');
    const btnPlay = section.querySelector('.video__play');
    const btnClose = section.querySelector('.video__close');

    function openVideo() {
    if (section.classList.contains('video-section--active')) return;
    section.classList.add('video-section--active');
    video.play().catch(() => {
        video.muted = true;
        video.play().catch(() => {});
    });
    }

    function restorePosterUsingLoad(v) {
        try {
                v.pause();
                v.currentTime = 0;
                v.load();
            } catch (err) {
        }
    }

    function restorePosterByRecreating(v) {
        const parent = v.parentNode;
        const clone = v.cloneNode(true);
        clone.preload = v.preload;
        clone.poster = v.poster;
        clone.muted = v.muted;
        parent.replaceChild(clone, v);
        return clone;
    }

    function closeVideo() {
    if (!section.classList.contains('video-section--active')) return;
    section.classList.remove('video-section--active');

    restorePosterUsingLoad(video);

    setTimeout(() => {
        const computed = window.getComputedStyle(video);
        if (!video.paused || video.currentTime !== 0) {
        const newVideo = restorePosterByRecreating(video);
        }
    }, 50);
    }

    btnPlay.addEventListener('click', (e) => { e.stopPropagation(); openVideo(); });
    btnClose.addEventListener('click', (e) => { e.stopPropagation(); closeVideo(); });
    video.addEventListener('click', (e) => {
    e.stopPropagation();
    if (!section.classList.contains('video-section--active')) openVideo();
    else video.paused ? video.play() : video.pause();
    });
    document.addEventListener('keydown', (e) => {
    if (e.key === 'Escape' && section.classList.contains('video-section--active')) closeVideo();
    });
    video.addEventListener('ended', closeVideo);

    //Accordion
    if (window.innerWidth >= 993) {
        $(".item-show").click(function () {
            let parent = $(this).parents(".item")
            if($(parent).hasClass("item--active")){
                $(".item").removeClass("item--active")
                $(".item-hidden").slideUp()
            }else{
                $(".item").removeClass("item--active")
                $(parent).addClass("item--active")
                $(".item-hidden").slideUp()
                $(this).siblings(".item-hidden").slideDown()
            }
        })
    }    

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })

    function closeModal() {
        $('.modal--active video').each(function () {
            this.pause();
            this.currentTime = 0;
        });
        
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

})